*{
  box-sizing: border-box;
}
.name{
  display: flex;
  padding: 0px 80px;
  background-color: white(6, 6, 6);
  color: black;


}
.name1{
 width: 50%;
 padding: 10px 0px;
 font-size: 20px;
}
.name2{
  width: 50%;
  padding: 60px 10px ;
  display:flex;
  justify-content: space-around;
  font-size: 20px;
}
.style{
  background-color: brown;
  color: white;
  border-radius: 50%;
  padding: 0px 20px;
  
}
.div1{
  display: flex;
  justify-content: space-around;
  padding: 50px 80px;
  background-color: black;
  width: 100%;
} 
.c1{
  display: flex;
  padding: 30px 90px 30px 60px;
  background-color: rgb(17, 16, 16);
  color: azure;

}
.c2{
  padding: 3px 40px;
  font-size: xx-large;
  font-weight: bolder;
} 
.design{
  background-color: black;
  padding: 20px 80px;
  display: flex;
  justify-content: space-around;
  color: antiquewhite;
}
.me{
  display: flex;
  justify-content: space-around;
  background-color: black;
  color: azure;
}

.last{
  padding: 40px 80px;
  background-color: rgb(8, 8, 8);
  display: flex;
}
.atlast{
  width: 50%;
  display: flex;
  margin: 0px 3px;
}
.last1{
  width: 50%;
  justify-content: flex-end;
  float: left;

}
.last2{
  width: 50%;
  justify-content: flex-end;
  float: left;
  color: azure;
  font-size: 17px;
}
.end{
  width: 50%;
  display: flex;
  margin: 0px 3px;
}
.col{
  display: flex;
  align-content: space-between;
  justify-content: space-between;
  padding: 10px 10px;
}
.adjust{
  display: flex;
  padding: 20px 80px;
  background-color: rgb(233, 242, 242);
}
